import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components

import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";




import styles from "assets/jss/material-kit-react/views/componentsSections/basicsStyle.js";

const useStyles = makeStyles(styles);

export default function GeneralSection() {
  const classes = useStyles();
  return (
    <div className={classes.sections}>
			<div className={classes.container}>
				<div className={classes.title}>
					<h2>Trainingszeiten</h2>	
				</div>
				<div className={classes.title}>
					<h4> Wir freuen uns auf jeden, der gerne einmal die Sportart Radball ausprobieren möchte. Egal ob jung oder alt. Der Jüngste ist bei uns 5 Jahre alt, der älteste Mitte 50. Ihr könnt einfach bei uns vorbeischauen oder mit uns Kontakt aufnehmen. Unsere Trainingszeiten sind: <br/> Immer Montags <br/> 18:00 - 19:00 Jugend <br
/> 19:00 - 21:00 Aktive
					</h4>	
				</div>
			</div>
		</div>
	);
}